import React, { useState } from "react";
import Styled from "styled-components";
import BackgroundLogin from "./image/background.png";
import axios from "axios";

const Main = Styled.div`
  background-image: url(${BackgroundLogin});
  background-size: 100% 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-color: rgba(10,23,55,0.4);
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const Input = Styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  align-self: flex-start;
  margin-top: 6vh;
  width: 50vw;
  height: 30vh;
  color: white;
  background-color: rgba(10,23,55,0.7);

  input {
    font-size: 2em;
  }
`;

function App() {
  const [link, setLink] = useState(undefined);

  const handleFile = async (event) => {
    try {
      const data = new FormData();

      data.append("file", event.target.files[0]);

      const result = await axios.put("http://localhost:3000/", data);

      setLink(result.data.link);
    } catch (error) {
      throw new Error(error.message);
    }
  };

  return (
    <Main>
      <Input>
        <input
          type='file'
          onChange={handleFile}
          accept='.gif,.jpg,.jpeg,.png,.doc,.docx'
        />
      </Input>
      {link && (
        <div>
          <img src={link} alt='imagem carregada' />
          <p>
            <a href={link}>Link do arquivo</a>
          </p>
        </div>
      )}
    </Main>
  );
}

export default App;
